@import (less) "color.less";

:host {
    position:relative;
    display: block;
}

.textfield-box {
    margin: 0 0 0.625em 0;
    padding: 0.375em 1em 0.375em 1em;
    color: @gray;
    border: 1px solid @darkest_gray;
    border-radius: 3px;
    font-size: 1em;
    height: 3.125em;
    box-shadow: none !important;
    &:focus {
        border: 2px solid @light_green;
        outline: 0;
        box-shadow: none !important;
    }
    &[disabled] {
        opacity: 0.5;
        background-color: @white;
    }
}

:host(.ng-dirty.ng-invalid.nbp-show-validation) {
    .textfield-box {
        border: 2px solid @red !important;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        &:focus {
            border: 2px solid @red;
        }
    }
}

.highlight-default {
    border:2px solid @yellow;
}

.round-borders {
    border-radius:25px;
}

.icon-inside{
  position:absolute;
  top:50%; 
  right:25px;
  font-size: 26px;
  transform: translateY(-50%);
  &:before{
    color:  @green;
  }
}

.contrasto-on {
    .input-label {
        color: @black;
    }
    .input-box {
        color: @black;
        border: 1px solid @black;
        &:focus {
            border: 2px solid @black;
        }
        &::-webkit-input-placeholder {
            color:@black;
        }
        &[disabled] {
            color: @white;
            background-color: @black;
            opacity: 1;
        }
    }
    .has-error {
        .input-box {
            border: 2px solid @black;
            -webkit-box-shadow: inset 0 0px 0px @black;
            box-shadow: inset 0 0px 0px @black;
            &:focus {
                border: 2px solid @black;
            }
        }
    }
    .yellow-border {
        border:2px solid @black;
    }
}